import React, { Component,ComponentType } from 'react'
import { Button } from 'antd';
import {Link} from 'react-router-dom';
import RouterView from '@/router/RouterView'

interface ICom {
    path: string,
    redirect?: string,
    component?: ComponentType,
    children?: ICom,
    isHoldup?: boolean,
}
interface Iprops {
    routes: Array<ICom>,
}
interface Istate {
    title: string
}

export default class Usershow extends Component<Iprops,Istate> {
    constructor (props: Iprops) {
        super(props);
        this.state = {
            title: ''
        }
    }
    render() {
        return (
            <div className="usershow">
                <div className="router-list">
                        <Button onClick={() => this.titleChange('')}>
                            <Link to="/home/usershow/userdata"></Link>
                        </Button>
                        <Button onClick={() => this.titleChange('')}>
                            <Link to="/home/usershow/iddata"></Link>
                        </Button>
                        <Button onClick={() => this.titleChange('api')}>
                            <Link to="/home/usershow/apioth">api</Link>
                        </Button>
                        <Button onClick={() => this.titleChange('api')}>
                            <Link to="/home/usershow/idnapi">api</Link>       
                        </Button>
                        <Button onClick={() => this.titleChange('')}>
                            <Link to="/home/usershow/viewoth"></Link>
                        </Button>
                        <Button onClick={() => this.titleChange('')}>
                            <Link to="/home/usershow/idnoth"></Link>
                        </Button>
                </div>
                <div className="title">{this.state.title}</div>
                <div className="view_3">
                    <RouterView routes={this.props.routes}/>
                </div>
            </div>
        )
    }
    titleChange (msg:string) {
        this.setState ({
            title: msg
        })
    }
}
