import React, { Component } from "react";
import { Table, Form, Select, Button } from "antd";
import { inject, observer } from "mobx-react";

interface Item {
  name: string;
  age: string;
  address: string;
  exam_name:string;
  subject_text:string;
  key: string;
}
interface IProps{
  testPaper?:any,
  [key:string]:any
}

@inject("testPaper")
@observer
class Examlist extends Component<IProps> {
  render() {
    const { examType,courseType,realCourseList } =this.props.testPaper;
    return (
      <div className="big-test">
        <h3></h3>
        <div className="test-header">
          <Form
            name="basic"
            initialValues={{ remember: true }}
            onFinish={this.onFinish}
            onFinishFailed={this.onFinishFailed}
          >
            <Form.Item>
              <Form.Item
                label=""
                style={{
                  display: "inline-flex",
                  width: "calc(30%)",
                  paddingTop: 50,
                }}
              >
                <Select
                  style={{ width: 120, marginLeft: -50 }}
                >
                  {examType.map((item:Item,index:number|string|undefined) => {
                    return (
                      <Select.Option value={item.exam_name} key={index}>
                          {item.exam_name}
                      </Select.Option>
                    );
                  })}
                </Select>
              </Form.Item>
              <Form.Item
                label=""
                style={{
                  display: "inline-flex",
                  width: "calc(30%)",
                  paddingTop: 50,
                }}
              >
                <Select
                  style={{ width: 120, marginLeft: -70 }}
                >
                 {courseType.map((item:Item,index:number|string|undefined) => {
                    return (
                      <Select.Option value={item.subject_text} key={index}>
                          {item.subject_text}
                      </Select.Option>
                    );
                  })}
                </Select>
              </Form.Item>
              <Form.Item
                style={{ display: "inline-flex", width: 100, paddingTop: 50 }}
              >
                <Button type="primary" htmlType="submit">
                  
                </Button>
              </Form.Item>
            </Form.Item>
          </Form>
        </div>
        <div className="test">
          <p></p>
          <Table
            rowClassName={() => "editable-row"}
            bordered
            dataSource={realCourseList.slice()}
            columns={this.state.columns}
          />
        </div>
      </div>
    );
  }
  state = {
    dataSource:[],
    columns: [
      {
        title: "",
        dataIndex: "name",
      },
      {
        title: "",
        dataIndex: "grade",
      },
      {
        title: "",
        dataIndex: "person",
      },
      {
        title: "",
        dataIndex: "start_time",
      },
      {
        title: "",
        dataIndex: "end_time",
      },
      {
        title: "",
        dataIndex: "operation",
        render: (text:object, record:object,index:Number) =>
          this.state.dataSource.length<1 ? <span onClick={()=>{this.goDetail(text,record,index)}} style={{color:'skyblue'}}></span> : null,
      },
    ],
    count: 2,
  };
 
  async componentDidMount() {
    //
    this.props.testPaper.getExamType();
    this.props.testPaper.allCourseType();
    this.props.testPaper.allCourseList();
  }
  onFinish = (values: any) => {
    console.log("Success:", values);
  };
  onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };
  goDetail=(text: Object,record:any,index:Number)=>{
  
      //
      this.props.history.push({
         pathname:'/home/examDetail',
         state:{
                  key:record.key
               }
      })
    
  }
}

export default Examlist;
