import React, { Component } from 'react'
import { _grade, _leixing } from "../../api/apiss"
interface Imsg {
    questions_type_id: string,
    questions_type_text: string,
    questions_type_sort: number,
}
interface IState {
    tg: string,
    show: boolean,
    gradedata: Array<Imsg>//
}
export default class Testlist extends Component<any, IState>{
    state = {
        tg: "",
        show: false,
        gradedata: []
    }
    async bian(questions: string) {
        let res = await _leixing(questions)
        if (res.data.code) {
            alert(res.data.msg)
        }
    }
    componentDidMount() {
        this.getList()
    }
    getList = async () => {
        let res = await _grade();
        console.log(res.data.data)
        console.log(this)
        this.setState({
            gradedata: res.data.data
        })
    }
    inputChange(e: InputEvent) {
        let el = (e.target as any).value
        this.setState({
            tg: el
        })
    }
    qd() {
        this.setState({
            show: false
        })
    }
    ying() {
        this.setState({
            show: true
        })
    }
    qx() {
        this.setState({
            show: false
        })
    }

    render() {
        return (
            <div className="Testlist">
                <div className="shang">
                    <p><button onClick={() => this.ying()}></button> </p>
                </div>
                <div className="xia">
                    <table>
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                //item:Imsg 
                                this.state.gradedata.map((item: Imsg, index) => {
                                    console.log(item)
                                    return (
                                        <tr key={index}>
                                            <th>{item.questions_type_id}</th>
                                            <th>{item.questions_type_text} </th>
                                            <th><button onClick={() => this.bian(item.questions_type_id)}></button> </th>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
                <div className="tan" style={{ display: this.state.show ? 'block' : "none" }}>
                    <div className="ds">
                        <p></p>
                        <p><input placeholder="" value={this.state.tg} onChange={(e) => this.inputChange(e as any)}></input> </p>
                        <p> <button className="qd" onClick={() => this.qd()}> </button>  <button onClick={() => this.qx()}></button> </p>
                    </div>
                </div>
            </div>
        )
    }

}
