import React, { Component } from "react";
import { _room, _grade, _zhou, _xiugai } from "../../../api/apiss";
//wangeditor 
import E from "wangeditor";
interface zhouDatas {
  questions_type_id: string;
  questions_type_text: string;
  questions_type_sort: number;
}
interface tiDataS {
  exam_id: string;
  exam_name: string;
}
interface xiuDataS {
  subject_text: string;
  subject_id: string;
  index: number;
}
interface State {
  list: Array<any>;
  tg: string;
  zhouData: Array<zhouDatas>;
  tiData: Array<tiDataS>;
  xiuData: Array<xiuDataS>;
  location: any;
  questions_id: string;
}

let editor: E | null = null;
//
let editor1: E | null = null;
export default class Testques extends Component<any, State> {
  state = {
    list: [],
    location: [],
    tg: "",
    zhouData: [],
    tiData: [],
    xiuData: [],
    questions_id: "",
  };
  xiala1 = "";
  xiala2 = "";
  xiala3 = "";
  componentDidMount() {
    //
    editor = new E("#box1");
    //
    editor1 = new E("#box2");
    //
    editor.create();
    editor1.create();
    this.getList();
    this.zhouList();
    this.leiList();
    console.log(this.props.location.state.obj);
    this.setState({
      tg: this.props.location.state.obj[0].title,
    });
    this.setState({
      questions_id: this.props.location.state.obj[0].questions_id,
    });
  }
  async getList() {
    let res = await _zhou();
    //
    console.log(res.data.data);
    this.setState({
      zhouData: res.data.data,
    });
  }
  async zhouList() {
    let res = await _grade();
    //
    console.log(res.data.data);
    this.setState({
      tiData: res.data.data,
    });
  }
  async leiList() {
    let res = await _room();
    //
    console.log(res.data.data);
    this.setState({
      xiuData: res.data.data,
    });
  }
  inputChange(e: InputEvent) {
    let el = (e.target as any).value;
    this.setState({
      tg: el,
    });
  }
  async getInputValue() {
    let title = this.state.tg;
    let exam_id = (this.xiala1 as any).value;
    let subject_id = (this.xiala2 as any).value;
    let questions_type_id = (this.xiala3 as any).value;
    let questions_id = this.state.questions_id;
    //
    let questions_answer = (editor as E).txt.text();
    let questions_stem = (editor1 as E).txt.text();
    let res = await _xiugai({
      title,
      exam_id,
      subject_id,
      questions_type_id,
      questions_answer,
      questions_id,
      questions_stem,
    });
    console.log(res);
    alert(res.data.msg);
  }
  render() {
    return (
      <div className="Testadd">
        <p></p>
        <p></p>
        <p>
          {" "}
          <input value={this.state.tg} onChange={(e) => this.inputChange(e as any)}></input>{" "}
        </p>

        <p></p>
        <div id="box1"></div>
        <p>
          {" "}
          <select
            className="xiala1"
            ref={(el) => {
              (this.xiala1 as any) = el;
            }}
          >
            {this.state.zhouData.map((item: tiDataS, index: number) => {
              return (
                <option value={item.exam_id} key={index}>
                  {item.exam_name}
                </option>
              );
            })}
          </select>
        </p>
        <p>
          {" "}
          {" "}
          <select
            className="xiala2"
            ref={(el) => {
              (this.xiala2 as any) = el;
            }}
          >
            {this.state.xiuData.map((item: xiuDataS, index: number) => {
              return (
                <option value={item.subject_id} key={index}>
                  {item.subject_text}
                </option>
              );
            })}
          </select>
        </p>
        <p>
          {" "}
          {" "}
          <select
            className="xiala3"
            ref={(el) => {
              (this.xiala3 as any) = el;
            }}
          >
            {this.state.tiData.map((item: zhouDatas, index: number) => {
              return (
                <option value={item.questions_type_id} key={index}>
                  {item.questions_type_text}
                </option>
              );
            })}
          </select>
        </p>
        <div id="box2"></div>
        <p>
          <button onClick={() => this.getInputValue()}></button>
        </p>
      </div>
    );
  }
}
