import React, { Component } from "react";
import { Form, Input, Button, Select, DatePicker } from "antd";
import { inject, observer } from "mobx-react";
import { _addTest } from "@/api/exam";
interface IItem{
  subject_id: string;
  exam_id: string;
  title:string;
  number: string;
  start_time: string;
  end_time:string;
  exam_name:string;
  subject_text:string;
}
interface IProps{
  [key:string]:any
}
@inject("testPaper")
@observer
export default class Examadd extends Component<IProps> {
  render() {
    const { examType, courseType } = this.props.testPaper;
    return (
      <div className="mAdd">
        <h3></h3>
        <div className="content">
          <Form
            name="nest-messages"
            onFinish={this.onFinish}
            style={{ paddingLeft: 20 }}
            initialValues={this.state.fields}
          >
            <Form.Item
              name="title"
              label=""
              rules={[{ required: true }]}
              style={{ width: 500, marginLeft: 20, paddingTop: 10 }}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label=""
              style={{ marginLeft: 20 }}
              name="subject_id"
            >
              <Select style={{ width: 120, marginLeft: 10 }}>
                {examType.map((item:IItem) => {
                  return (
                    <Select.Option value={item.exam_id} key={item.exam_id}>
                      {item.exam_name}
                    </Select.Option>
                  );
                })}
              </Select>
            </Form.Item>
            <Form.Item
              label=""
              style={{ marginLeft: 20 }}
              name="exam_id"
            >
              <Select
                style={{ width: 120, marginLeft: 10, display: "flex-block" }}
              >
                {courseType.map((item:IItem) => {
                  return (
                    <Select.Option
                      value={item.subject_id}
                      key={item.subject_id}
                    >
                      {item.subject_text}
                    </Select.Option>
                  );
                })}
              </Select>
            </Form.Item>

            <Form.Item
              style={{ width: 250, marginLeft: 20 }}
              name="number"
              label=""
              rules={[{ required: true }]}
            >
              <Input />
            </Form.Item>
            
           
           <Form.Item style={{display:'flex'}}>
                  <Form.Item name="start_time" label="" style={{display: "flex-block"}}>
                    <DatePicker />
                    </Form.Item>
                    <span>-</span>
                    <Form.Item name="end_time">
                    <DatePicker />
                 </Form.Item>
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }
  state = {
    questions:[],
    fields: {
      subject_id: "",
      exam_id: "",
      title: "",
      number: "",
      start_time: "",
      end_time: "",
    },
  };
  onFinish = async (values:IItem) => {
    //   
    values = Object.assign(this.state.fields, values, {
      number: values.number,
      start_time: Number(values.start_time).toString(),
      end_time: Number(values.end_time).toString(),
    });
    const res = await _addTest(this.state.fields);
     this.setState({
      questions:res.data.questions
     })
    // 
    let exam_exam_id=res.data.data.exam_exam_id;
     console.log(res.data);
    if (res.data.code === 1) {
      this.props.history.push({
        pathname: "/home/examNew",
        state: {
          title:values.title,
          time:values.end_time,
           exam_exam_id:exam_exam_id,
           questions:this.state.questions
        },
      });
    }
  };
}
